<template>
  <div class="sentences">
    <div class="sentences-min">
      <p>Word Min :</p>
      <input
        type="number"
        min="1"
        v-model="dataRowItemOption.wordMin"
        @change="setDataRowItemOption"
        class="ds-input"
      />
    </div>
    <div class="sentences-max">
      <p>Word Max :</p>
      <input
        type="number"
        min="1"
        v-model="dataRowItemOption.wordMax"
        @change="setDataRowItemOption"
        class="ds-input"
      />
    </div>
    <div class="sentences-min">
      <p>Count Min :</p>
      <input
        type="number"
        min="1"
        v-model="dataRowItemOption.countMin"
        @change="setDataRowItemOption"
        class="ds-input"
      />
    </div>
    <div class="sentences-max">
      <p>Count Max :</p>
      <input
        type="number"
        min="1"
        v-model="dataRowItemOption.countMax"
        @change="setDataRowItemOption"
        class="ds-input"
      />
    </div>
    <div class="sentences-string">
      <label
        ><input
          type="checkbox"
          v-model="dataRowItemOption.korean"
          @change="setDataRowItemOption"
          value="true"
        />한글</label
      >
      <label
        ><input
          type="checkbox"
          v-model="dataRowItemOption.uppercase"
          @change="setDataRowItemOption"
          value="true"
        />대문자</label
      >
      <label
        ><input
          type="checkbox"
          v-model="dataRowItemOption.lowercase"
          @change="setDataRowItemOption"
          value="true"
        />소문자</label
      >
      <label
        ><input
          type="checkbox"
          v-model="dataRowItemOption.special"
          @change="setDataRowItemOption"
          value="true"
        />특수문자</label
      >
      <label
        ><input
          type="checkbox"
          v-model="dataRowItemOption.number"
          @change="setDataRowItemOption"
          value="true"
        />숫자</label
      >
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow
      ? computed(() => props.dataRow.item.itemOption)
      : ref({
          wordMin: 1,
          wordMax: 2,
          countMin: 1,
          countMax: 2,
          korean: false,
          uppercase: false,
          lowercase: false,
          special: false,
          number: false,
        });

    store.commit('setDataRowItemOption', {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value,
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

